import React, { useState } from "react";
import AddIcon from "@material-ui/icons/Add";
import Fab from "@material-ui/core/Fab";
import Zoom from "@material-ui/core/Zoom";

function CreateArea(props) {

    const [note, setNote] = useState({
        title: "",
        content: ""
    })

    const [isExpanded, setExpanded] = useState(false)

    function expand() {
        setExpanded(true)
    }

    function handleChange(e) {
        const { name, value } = e.target;

        setNote(prevNotes => {
            return (
                {
                    ...prevNotes,
                    [name]: value
                }
            )
        })
    }

    function handleSubmit(e) {
        e.preventDefault();
        props.onAdd(note);
        setNote({ title: "", content: "" })

    }

    return (
        <div>
            <form className="create-note">
                {isExpanded && <input name="title" onChange={handleChange} value={note.title} placeholder="Title" />}
                <textarea name="content" onChange={handleChange} onClick={expand} value={note.content} placeholder="Take a note..." rows={isExpanded ? "3" : "1"} />
                <Zoom in={isExpanded}>
                    <Fab onClick={handleSubmit}><AddIcon /></Fab>
                </Zoom>

            </form>
        </div>
    );
}

export default CreateArea;